import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';
import '../app.css';
// import * as Scroll from 'react-scroll';
import { Link } from 'react-scroll';

class LinkBar extends Component {
  state = {
    isOpen: false,
  };

  toggleClass = () => {
    this.setState({ isOpen: !this.state.isOpen });
  };

  render() {
    const element_ul = this.state.isOpen ? "open" : "";
    const element_button = this.state.isOpen ? "open nav-hamburger" : "nav-hamburger";
    // const navbar = this.state.isOpen ? "navbar-open navbar" : "navbar";

    return (
      <header className='navbar'>
        <div className="navbar-header">
          <a className="nav-logo" href="/">Bazaar</a>
          <button className={element_button} onClick={this.toggleClass}>
            <span className="nav-stripe"></span>
            <span className="nav-stripe"></span>
            <span className="nav-stripe"></span>
          </button>
        </div>
        <nav className="nav-menu">
          <ul className={element_ul}>
            <li><NavLink exact to="/" activeClassName="active">Home</NavLink></li>
            <li><Link activeClass="active" to="products" spy={true} smooth={true} offset={50} duration={500} delay={200}>
          Products
        </Link></li>
            <li><NavLink exact to="/#" activeClassName="active">FAQ's</NavLink></li>
            <li><NavLink exact to="/#" activeClassName="active">Cart (0) <i class="fa fa-shopping-cart" aria-hidden="true"></i></NavLink></li>
            <li><a href="/login">Log In</a></li>
            <li><a href="/register">Sign Up</a></li>
            {/*<li><a href="/logout">Log Out</a></li>*/}
          </ul>
        </nav>
      </header>
      );
  }
}

export default LinkBar;


